  background-color: #1e1f23;
}

.detail-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.navbar {
  margin-top: -20px;
  width: 500px;
  height: 40px;
  display: flex;
  justify-content: flex-start;
  padding: 10px;
  background-color: #1e1f23;
}

.back-button {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center;
  background-size: contain;
  border: none;
  width: 30px; /* 버튼 크기 조정 */
  height: 30px; /* 버튼 크기 조정 */
  cursor: pointer;
  border-radius: 90px;
  margin-bottom: 8px;
}

.image-container {
  margin-top: 90px;
  width: 150px; /* 컨테이너 너비 */
  height: 250px;
  display: flex;
  justify-content: center; /* 가운데 정렬 */
  align-items: center; /* 세로 가운데 정렬 */
}

.product-image {
  margin-top: 20px;
  width: 100%; /* 이미지가 컨테이너 너비에 맞춰 조정됨 */
  height: auto; /* 비율에 맞춰 높이 조정 */
  object-fit: contain; /* 이미지 비율 유지하며 컨테이너에 맞춤 */
}

.product-info h1 {
  margin-top: 20px;
  text-align: left;
  color: #FFFFFF;
  font-family: 'f5';
  font-size: 20pt;
  font-weight: bold;
}

.product-info p {
  margin-top: 10px;
  text-align: right;
  color: #FFFFFF;
  font-family: 'f4';
  font-size: 18pt;
}

.view-product-button {
  margin-top: 10px;
  background-color: #20edc7;
  color: #1e1f23;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  width: 220px;
  border-radius: 12px;
  font-family: 'f5';
}

.view-product-button:hover {
  background-color: #FFFFFF;
  color: #000000;
}

@font-face {
  font-family: 'f4';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
}
@font-face {
  font-family: 'f5';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format('truetype');
}
